import React, { useState } from "react";
import _ from "lodash";
import config from "../config";
import Link from "next/link";

const routers = _.get(config, "routers");

function HomePage() {
  return (
    <div>
      Welcome to Next.js!
      <h1>Menu</h1>
      {_renderRouters(routers)}
    </div>
  );
}

const _renderRouters = routers => {
  const [isShow, setToggle] = useState(false);

  if (!_.isEmpty(routers)) {
    return _.map(routers, (group, idxGroup) => {
      return (
        <div key={idxGroup}>
          <h3 onClick={() => setToggle(true)}>{_.get(group, "text")}</h3>
          {isShow && (
            <ul>
              {!_.isEmpty(group.subRouters) &&
                _.map(group.subRouters, (router, index) => {
                  return (
                    <li key={index}>
                      <Link
                        href={_.get(router, "href")}
                        as={`${_.get(router, "href")}${_.get(router, "id")}`}
                      >
                        {_.get(router, "text")}
                      </Link>
                    </li>
                  );
                })}
            </ul>
          )}
        </div>
      );
    });
  }
};

export default HomePage;
